

























    import { Component, Vue } from 'vue-property-decorator'
    import { axiosProxy, checkErrorCode } from '@/assets/js/public'

    @Component({
        components: {

        },
        props: {

    		},
    })
    export default class cmsHeader extends Vue {
        menuList: any[] = []
        treeList: any[] = []
        tempArray: any[] = []

        getMenuList () {
            // @ts-ignore
            let url = this.$collectUrl('menuListGet')
            axiosProxy.get(url).then(res => {
                checkErrorCode(res).then(respond => {
                    this.menuList = res.data.data
                    // todo 各站点的排除限制还没处理
                    this.setTree(this.menuList)
                })
            }).catch(error => {
                console.warn(error)
            })
        }

        setTree (array) {
            for (let i = 0; i < array.length; i++) {
                array[i].childrenNodes = []
            }
            for (let i = 0; i < array.length; i++) {
                array[i].childrenNodes = []
                if(array[i].parentId) {
                    for (let j = 0; j < array.length; j++) {
                        if(array[i].parentId == array[j].id) {
                            array[j].childrenNodes.push(array[i])
                            if(array[j].id == 34) {
                                this.tempArray = array[j].childrenNodes
                            }
                        }
                    }
                } else {
                    this.treeList.push(array[i])
                    for (let i = 0; i < this.treeList.length; i++) {
                        if(this.treeList[i].id == 34) {
                            this.treeList[i].childrenNodes = this.tempArray
                        }
                    }
                }
            }
        }

        gotoPage (href) {
            let result = this.$router['history'].current.fullPath
            if(href !== this.$router['history'].current.fullPath) {
                this.$router.push({
                    path: href
                })
                this.$emit('click')
            } else {
                this.$errorMessage('点击了当前页面')
            }
        }

        mounted () {
            this.getMenuList()
		    }
    }
