import Vue from 'vue'
import Router from 'vue-router'
import Main from './components/main.vue'
import Body from './components/body.vue'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '欢迎',
      component: Body,
      redirect: '/welcome',
      children: [{
        path: '/welcome',
        meta: {
          title: '欢迎'
        },
        component: resolve => { require(['@/components/welcome.vue'], resolve) }
      }],
    },
    {
      path: '/about',
      name: 'about',
      component: resolve => { require(['@/views/About.vue'], resolve) }
    },
    {
      path: '/block/list',
      name: 'blockList',
      component: Body,
      redirect: '/block/list',
      children: [{
        meta: {
          title: '区块管理'
        },
        path: '/block/list',
        component: resolve => { require(['@/views/block/list.vue'], resolve) }
      }]
    },
    {
      path: '/content/list',
      name: 'contentList',
      component: Body,
      redirect: '/content/list',
      children: [{
        meta: {
          title: '栏目内容管理'
        },
        path: '/content/list',
        component: resolve => { require(['@/views/content/content/list.vue'], resolve) }
      }]
    }
  ]
})
