import axios from 'axios'
import Vue from 'vue'

// 重新封装axios方法
let axiosProxy: any = {}
axiosProxy = axios.create({
    headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Access-Control-Allow-Origin': '*'
    },
    timeout: 1000 * 60 * 5,
    withCredentials:true
})

// 错误提示，有系统提示显示系统的，没有系统的显示我定义的
let showErrorMessage = (msg: any, res: any): any => {
    if(res) {
        Vue.prototype.$errorMessage(res)
    } else {
        Vue.prototype.$errorMessage(msg)
    }
}

// 请求拦截器
let checkErrorCode = (res: any): any => {
    let p = new Promise((resolve, reject) => {
        switchError(res.data.errorCode, res).then((result) => {
            return result()
        }).then((data) => {
            if(data) {
                resolve(res)
            }
        })
    })
    return p
}

// 处理各种请求的回调
let switchError = (res: any, resObj: any): any => {
    // 定义请求码对应的对象接口
    interface ErrorType {
        4000(): void,
        0(): void,
        10002(): void,
        10006(): void,
        10007(): void,
        500(): void,
        400(): void
    }

    // 具体处理各种请求的回调办法
    let p = new Promise((resolve, reject) => {
        let errorObj : ErrorType = {
            4000: () => {
                window.sessionStorage.clear()
                let url = resObj.data.data.loginUrl? resObj.data.data.loginUrl: null
                showErrorMessage(null, resObj.data.errorMessage)
                if(url) {
                    setTimeout(() => {
                        window.location.href = url
                    }, 500)
                }
            },
            0: () => {
                let p1 = new Promise((resolve, reject) => {
                    resolve('200 case')
                })
                return p1
            },
            10007: () => {
                showErrorMessage('用户未激活', resObj)
            },
            10002: () => {
                showErrorMessage('参数错误', resObj)
            },
            10006: () => {
                showErrorMessage('数据已存在', resObj)
            },
            500: () => {
                showErrorMessage('系统错误', resObj)
            },
            400: () => {
                showErrorMessage('系统错误', resObj)
            }
        }
        if(typeof(errorObj[res]) !== 'function') {
            resolve(false)
        } else {
            resolve(errorObj[res])
        }
    })
    return p
}

let timestampToTime = (timestamp: any): any => {
    var date = new Date(timestamp)
    var Y = date.getFullYear() + '/'
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '/'
    var D
    if(date.getDate() < 10) {
        D = '0' + date.getDate() + ' '
    } else {
        D = date.getDate() + ' '
    }
    var h
    if(date.getHours() < 10) {
        h = '0' + date.getHours() + ':'
    } else {
        h = date.getHours() + ':'
    }
    var m
    var s

    if(date.getSeconds() < 10) {
        s = '0' + date.getSeconds()
    } else {
        s = date.getSeconds()
    }

    if(date.getMinutes() < 10) {
        m = '0' + date.getMinutes() + ':'
    } else {
        m = date.getMinutes() + ':'
    }
    return Y + M + D + h + m + s
}


