import Vue from 'vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import globalData from "@/assets/js/global"
import App from './App.vue'

// 自定义的全局变量能挂在vue原型链上
declare module 'vue/types/vue' {
    interface Vue {
        $api: any,
        $baseUrl: any,
        $errorMessage: any,
        $successMessage: any
    }
}

Vue.use(globalData)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
