import { Component, Vue } from 'vue-property-decorator'

import { axiosProxy, checkErrorCode, showErrorMessage } from '@/assets/js/public'

interface UserInfoObj {
    userName: string,
    userAvatar: string,
    userSort: number
}

@Component({

})

export default class About extends Vue {
    siteLogo: any = ''
    siteName: any = ''
    siteList: any[] = []
    activeId: number = 1
    userInfo: UserInfoObj = {
        userName: '',
        userAvatar: '',
        userSort: 0
    }

    mounted () {
        this.getUserInfo().then((result) => {
            return this.switchType(result)()
        }).then((data) => {
            return this.getCurrentSite(data)
        }).then((id) => {
            return this.getSiteDetail(id)
        })
    }

    getUserInfo() {
        // @ts-ignore // 忽略这种报错 Property 'xxx' does not exist on type 'xxx'
        let url = this.$collectUrl('user')
        let p = new Promise((resolve, reject) => {
            axiosProxy.get(url).then(res => {
                checkErrorCode(res).then(req => {
                    this.$set(this.userInfo, 'userAvatar', res.data.data.headShot)
                    this.$set(this.userInfo, 'userName', res.data.data.userName)
                    this.$set(this.userInfo, 'userSort', res.data.data.type)
                    localStorage.setItem('userName',this.userInfo.userName)
                    localStorage.setItem('userType',res.data.data.role)
                    localStorage.setItem('userSpId', res.data.data.spId)
                    localStorage.setItem('userSort', res.data.data.type)
                    localStorage.setItem('_user_id_param', res.data.data.id)
                    localStorage.setItem('userMail', res.data.data.email)
                    resolve(res.data.data.type)
                })
            })
        })
        return p
    }

    getCurrentSite (data) {
        let p = new Promise((resolve, reject) => {
            let currentSite:any = data.filter(item => item.current === true)[0]
            if(currentSite) {
                this.siteName = currentSite.shortName
                this.siteLogo = currentSite.logo
                this.activeId = currentSite.id
                localStorage.setItem('_site_id_param', currentSite.id)
                localStorage.setItem('_site_name_param', currentSite.shortName)
                resolve(currentSite.id)
            } else {
                reject('出错了')
            }
        })
        return p
    }

    switchType (switchItem) {
        interface Types {
            0(): void,
            1(): void,
            2(): void
        }

        let switchObj : Types = {
            0: () => {
                // this.routerLink('/sp/edit', 'registSp', res.data.data.id, res.data.data)
                console.log('sp edit')
            },
            1: () => {
                return this.getSiteList()
            },
            2: () => {
                console.log('sp edit')
            }
        }
        if(typeof(switchObj[switchItem]) !== 'function') {
            return false
        } else {
            return switchObj[switchItem]
        }
    }

    getSiteList () {
        // @ts-ignore
        let url = this.$collectUrl('siteList')
        let p = new Promise((resolve, reject) => {
            axiosProxy.get(url).then(res => {
                checkErrorCode(res).then(respond => {
                    this.siteList = res.data.data
                    window.localStorage.setItem('siteList', JSON.stringify(res.data.data))
                    resolve(res.data.data)
                })
            }).catch(error => {
                console.warn(error)
            })
        })
        return p
    }

    getSiteDetail (id) {
        // @ts-ignore
        let url = this.$collectUrl('site') + '/' + id
        axiosProxy.get(url).then(res => {
            checkErrorCode(res).then(respond => {
                window.localStorage.setItem('_main_sp_id', res.data.data.spId)
                window.localStorage.setItem('_user_avatar', res.data.data.logo)
            })
        }).catch(error => {
            console.warn(error)
        })
    }
}
