






















import { Component, Vue } from 'vue-property-decorator'
import {mixins} from 'vue-class-component'
import { axiosProxy, checkErrorCode, showErrorMessage } from '@/assets/js/public'
import Submenu from '@/components/subMenu.vue'
import Base_ts from './base'
import { Tab, Tabs, Button, Grid, GridItem, Dialog, ActionSheet, Popup } from 'vant'
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Button)
Vue.use(Grid)
Vue.use(GridItem)
Vue.use(ActionSheet)
Vue.use(Popup)

@Component({
    components: {
        Submenu
    },
    mixins: [Base_ts]
})

export default class cmsHeader extends Vue {
    actions: any[] = [{ name: '用户中心', value: 1 }, { name: '网家家首页', value: 2 }, { name: '退出', value: 3 }, { name: '菜单', value: 4 }]
    showActionSheet: boolean = false
    showPopup: boolean = false

    openActionSheet () {
        this.showActionSheet = true
    }

    closePopup () {
        this.showPopup = false
    }

    switchSheeet (sheetItem) {
        interface Sheets {
            3(): void,
            1(): void,
            2(): void,
            4(): void
        }

        let switchObj : Sheets = {
            3: () => {
                this.logout()
            },
            1: () => {
                window.location.href = "http://console.open.cnlive.com/open/console/user/info"
            },
            2: () => {
                window.location.href = "http://wjjh5test.cnlive.com/news.html"
            },
            4: () => {
                this.showActionSheet = false
                this.showPopup = true
            }
        }
        if(typeof(switchObj[sheetItem]) !== 'function') {
            return false
        } else {
            return switchObj[sheetItem]
        }
    }

    selectSheet (val) {
        this.switchSheeet(val.value)()
    }

    logout () {
        Dialog.confirm({
            title: '标题',
            message: '弹窗内容',
        }).then(() => {
            window.localStorage.clear()
            window.location.href = "http://cmstest.cnlive.com:8768/logout"
        }).catch(() => {
            setTimeout(() => {
                showErrorMessage('您取消了登出操作', null)
            }, 500)
        })
    }

    siteChange (siteVal) {
        // @ts-ignore
        let url = this.$collectUrl('siteChange')
        axiosProxy.put(url + '/' + siteVal).then(res=>{
            checkErrorCode(res).then(respond => {
                window.location.reload()
            })
        }).catch(error => {
            console.warn(error)
        })
    }

    shiftSite (val, name) {
        window.sessionStorage.clear()
        let userSort = window.localStorage.getItem('userSort')
        if(Number(userSort) !== 2) {
            window.localStorage.setItem('_site_id_param', val)
            // this.$router.push("/welcome")

            // 暂时还不知道哪里设置的
            // window.localStorage.removeItem('treeBread')
            this.siteChange(val)
        }
    }
}
